.program {
  box-sizing: border-box;
  padding: 70px 8px 0;
}

.program__heading {
  box-sizing: border-box;
  margin: 0;
  @include bold(24px, 1.244em, $black);
  text-align: center;
}

.program__point {
  box-sizing: border-box;
  margin: 0;
  @include regular(14px, 1.436em, $blue);
  letter-spacing: 0.05em;
  font-weight: 600;
}

.program__description {
  box-sizing: border-box;
  margin: 0;
  @include regular(14px, 1.436em, $lightgrey);
}

.program__watch-full {
  box-sizing: border-box;
  @include regular(14px, 2em, $blue);
  letter-spacing: 0.05em;
  text-decoration-style: dashed;
  font-weight: 600;
  text-transform: uppercase;
}

.program__benefits {
  display: none;
}
